import './App.css';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Nav from './components/nav';
import userLogin from './components/login/login';

function App() {
  return (
    <Router>
      <div>
        <Route path="/" exact component={Nav} />
        <Route path="/login" component={userLogin} />
      </div>
    </Router>
  );
}

export default App;
